
import { defineComponent } from 'vue';
import { mapGetters } from 'vuex';

export default defineComponent({
  name: 'SuccessPage',
  data: () => ({
    theme: '' as any,
    description: '' as any,
  }),
  computed: {
    ...mapGetters(['getSuccess']),
  },
  mounted() {
    const route = this.$route;

    if (route.query) {
      this.theme = route.query.request;
      this.description = route.query.desc;
    }
  },
});
